import React, { useState } from 'react'
import Color from "color"

export default class TypedText extends React.Component{

    constructor(props){
        super(props);

        this.state = {
            speed: props.speed || 1,
            textStyle: props.textStyle || {color:"white"},
            text: props.text,
            renderText: "",
            renderItems : [],
            index: -1,
            char: "",
        }
    }

    componentDidMount() {
        this.write();
    }

    write(){
        const text = this.state.text;
        const index = this.state.index + 1;
        

        if(text.indexOf("\n") === index){
            this.state.renderItems.push(this.state.renderText)
            this.state.renderItems.push("\n")
            this.state.renderText = "";

            this.applyWrite(index, "", 500);
            return;
        }

        const _char = text.charAt(index)       
        this.applyWrite(index, _char, _char === " " ? 250 : 150);
    }

    applyWrite(index, char, wait){
        this.setState({
            index,
            renderText: this.state.renderText + this.state.char,
            char,
        })

        if(index < this.state.text.length)
            setTimeout(() => this.write(), wait / this.state.speed)
    }

    render(){
        return (
        <div style={this.props.style}>  
            {this.state.renderItems.map((c, index) => 
                c === "\n" ? <br/> :
                <span key={index - 1} style={this.state.textStyle}>{c}</span>
            )}
            <span style={this.state.textStyle}>{this.state.renderText}</span>
            <span style={{
                color: Color(this.state.textStyle.color).negate(),
                backgroundColor:this.state.textStyle.color
                }}>{this.state.char}</span>
        </div>
        )
    }
}