import React from "react";
import Tabs from './tabs'
import './style.css'

export default class TabHandler extends React.Component{

    constructor(props){
        super(props);

        this.state = {
            tabs: this.props.tabs,
            activeTab: -1,
        }
    }

    onChangeTab(tabKey){
        this.setState({activeTab:tabKey});
    }

    render(){
        const tab = this.state.activeTab;
        const hasTab = this.props.children && tab > -1 && tab < this.props.children.length;
        return (
        <div className="tabHandler">
            <Tabs tabs={this.state.tabs} onTabChange={(key, selected) => this.onChangeTab(selected ? key : -1)} />
            <div className={"tabContentContainer" + (hasTab ? " blur" : "")}>
                {hasTab ? this.props.children[tab] : <div />}
            </div>
        </div>
        )
    }
}